<template>
  <div class="channel">
    <div class="loading" v-show="showLoading === true">
      <img src="../assets/loading.gif" alt="" />
      页面加载中...
    </div>
    <div class="headerBox">
      <div class="header">
        <!-- <div class="headerBox"> -->
        <div class="mark"></div>
        <div
          class="logo"
          @click="$router.path != '/tuijian' ? $router.push({ path: '/' }) : ''"
        ></div>
        <div class="search">
          <svg
            t="1664715880181"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="5518"
            width="48"
            height="48"
          >
            <path
              d="M851.710029 803.043632c67.147306-78.301344 107.720374-180.059935 107.720373-291.298435 0-247.27171-200.453405-447.724091-447.724091-447.724091S63.981197 264.473487 63.981197 511.745197s200.453405 447.724091 447.724091 447.724091c111.2385 0 212.997092-40.573067 291.298435-107.720374l97.632622 97.632621c13.449313 13.449313 35.255968 13.449313 48.706305 0 13.449313-13.449313 13.449313-35.255968 0-48.706305l-97.632621-97.631598z m-67.805292-19.10001l-22.499434 19.294439c-34.062793 29.209252-72.504314 52.006468-114.25623 67.759243-43.206034 16.301268-88.775906 24.566512-135.441739 24.566512-102.521978 0-198.907189-39.924291-271.40127-112.417349s-112.417349-168.879292-112.417349-271.40127 39.924291-198.907189 112.417349-271.40127S409.184333 127.926578 511.706311 127.926578c102.521978 0 198.907189 39.924291 271.40127 112.417349s112.417349 168.879292 112.417349 271.40127c0 46.666856-8.265244 92.236727-24.566512 135.441738-15.752776 41.75294-38.549991 80.193437-67.759243 114.256231l-19.294438 22.500456z"
              fill="#bfbfbf"
              p-id="5519"
            ></path>
          </svg>
          <input type="text" placeholder="搜索" @focus="showSearch()" />
        </div>
        <div class="openBtn">打开app</div>
        <!-- </div> -->
      </div>
    </div>
    <!-- 左边列表 -->
    <!-- @scroll="scrollChange" -->
    <ul class="left-content" ref="leftBox">
      <li class="item" v-for="(o, i) in categoryList" :key="o.name">
        <a
          class="sort"
          :class="{ active: i === arrIndex }"
          @click="
            arrIndex = i;
            changeNavigationBar(i);
          "
          id="vital1"
          >{{ o?.name }}</a
        >
      </li>
    </ul>
    <!-- 右边列表 -->
    <div class="bigbox" @scroll="scrollChange">
      <div class="box" ref="rightBox">
        <div class="right-content" v-for="(o, i) in categoryList" :key="o.name">
          <div
            class="title"
            @click="
              $router.push({
                path: '/channel',
                query: { id: o.id },
              })
            "
          >
            <p :class="{ addclass: arrIndex === i }" :ref="`conRef${i}`">
              {{ o.name }}
            </p>
            <i class="iconfont icon-icon_arrow_right"></i>
          </div>
          <div class="text">
            <div class="text-i" v-for="item in o?.channels" :key="item.channelId">
              <span
                @click="
                  $router.push({
                    path: `/channel/${o.id}`,
                  })
                "
                >{{ item.channelName }}</span
              >
              <!-- query: {
                      id: o.id,
                      subCategoryCode: item.relationMetadataValueId,
                      naem: o.name,
                    }, -->
            </div>
          </div>
        </div>
      </div>
    </div>
    <transition name="showPage">
      <searchPage
        class="searchPotion"
        v-show="showSearchPage"
        @Close-Page="closeSearch()"
        :showSearchPage="showSearchPage"
      />
    </transition>
  </div>
</template>

<script>
import SearchPage from "@/components/searchPage.vue";
const creatArray = (length) => Array.from(new Array(length), (val, i) => `item${i + 1}`);
export default {
  components: {
    SearchPage,
  },
  data() {
    return {
      categoryList: [],
      sortIndex: 0,
      list: creatArray(9),
      scrollNum: 0,
      indexList: [],
      ElH: [],
      ElH2: [],
      arrIndex: 0,
      arrIndex2: 0,
      showLoading: true,
      showSearchPage: false,
    };
  },
  methods: {
    showSearch() {
      this.showSearchPage = true;
      document.querySelector("body").style.overflow = "hidden";
    },
    closeSearch() {
      this.showSearchPage = false;
      document.querySelector("body").style.overflow = "initial";
    },
    changeNavigationBar(i) {
      document.querySelector(".bigbox").scrollTo({
        top: this.ElH[i],
      });
    },
    // 点击点击到第i个标签,取elh中的第几个值，滚动到第几个
    scrollChange: function (event) {
      this.scrollNum = event.srcElement.scrollTop;
    },
  },

  created() {
    this.showLoading = true;
    this.axios
      .get("https://m.ximalaya.com/revision/metadata/v2/group/all?includeChannels=true")
      .then((res) => {
        this.showLoading = false;
        this.categoryList = res.data.data.groups;
        this.$nextTick(() => {
          //遍历这个组里面的所有子元素的高度 赋值到arr
          let arr = [...this.$refs.rightBox.getElementsByClassName("right-content")].map(
            (o) => o.offsetHeight
          );
          let arr2 = [...this.$refs.leftBox.getElementsByClassName("item")].map(
            (o) => o.offsetHeight
          );

          this.ElH = arr.map((o, i, a) => {
            // debugger
            //定义一个初始值为 0
            let tmp = 0;
            if (i) {
              //a:原数组 即原来的数组
              //循环到第几个的时候就往原数据截取多少个 从左往右加起来
              tmp = a.slice(0, i).reduce((n, t) => n + t);
            } else {
              //初始值为0
              tmp = 0;
            }
            return tmp;
          });
          this.ElH2 = arr2.map((o, i, a) => {
            let tmp = 0;
            if (i) {
              tmp = a.slice(0, i).reduce((n, t) => n + t);
            } else {
              tmp = 0;
            }
            return tmp;
          });
        });
      });
  },
  watch: {
    scrollNum: function (n) {
      //查找下标
      let index = this.ElH.findIndex((o) => {
        // console.log(i);
        return o >= n;
      });
      //绑定位置给标到class来判断
      this.arrIndex = index;
      //查找下标
      let index2 = this.ElH.findIndex((o) => {
        // console.log(i);
        return o >= n;
      });
      //让他上升的高度与上面得出来的数组结果一致
      document.querySelector(".left-content").scrollTo({
        top: this.ElH2[index2],
      });
      //绑定位置给标到class来判断
      this.arrIndex2 = index2;
    },
  },
};
</script>

<style lang="less" scoped>
.headerBox {
  width: 100%;
  height: 50rem;
  background-color: #fff;
  position: fixed;
  top: 0;
  z-index: 1;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 10;
  display: flex;
  align-items: center;
  margin: 10rem 0;
  padding: 0 10rem;
  height: 30rem;
  // .headerBox {
  //   height: 50rem;
  //   background: #fff;
  // }
  .logo {
    flex-shrink: 0;
    width: 60rem;
    height: 100%;
    background-image: url(../assets/3.png);
    background-position: -25rem 2rem;
    background-size: 250rem;
    // margin-right: 10rem;
  }
  ::placeholder {
    color: #989898;
  }
  .search {
    width: 100%;
    position: relative;
    background-color: #eff1f4;
    border-radius: 50rem;
    margin: 0 10rem;
    svg {
      position: absolute;
      top: 50%;
      left: 8rem;
      transform: translateY(-50%);
      width: 15rem;
      height: 15rem;
    }
    input {
      border: 0;
      outline: 0;
      padding: 8rem 0 8rem 30rem;
      background-color: transparent;
    }
  }
  .openBtn {
    flex-shrink: 0;
    line-height: 10rem;
    padding: 10rem;
    height: 100%;
    border: 1rem solid #f66847;
    color: #f66847;
    border-radius: 50rem;
  }
}
.loading {
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 100;
  font-size: 15rem;
  img {
    margin-top: 100rem;
  }
}
.channel {
  position: relative;
  // margin-top: 55rem;
  height: calc(100% - 55rem);
  .left-content {
    width: 90rem;
    position: fixed;
    top: 50rem;
    bottom: 0;
    width: 90rem;
    overflow: scroll;
    &::-webkit-scrollbar {
      display: none;
    }
    .item {
      .sort {
        display: block;
        text-align: center;
        font-size: 14rem;
        padding: 15rem 0;
        padding-left: 5rem;
        line-height: 20rem;
        color: #72727b;
        position: relative;
        &::after {
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          border-bottom: 1rem solid #d7d7d7;
          transform: scaleY(0.5);
          transform-origin: 50% 100%;
        }
      }
      .active {
        position: relative;
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          width: 5rem;
          background-color: #f86442;
        }
      }
    }
  }
  .bigbox {
    position: fixed;
    top: 50rem;
    bottom: 0;
    right: 0;
    overflow-y: scroll;
    background-color: #ffffff;
    width: 285rem;
    .box {
      .right-content {
        .title {
          margin-left: 15rem;
          display: flex;
          align-items: center;
          line-height: 50rem;
          height: 50rem;
          border-bottom: 0.5rem solid #e8e8e8;
          p {
            flex-grow: 1;
            color: #40404c;
            font-weight: 600;
            font-size: 14rem;
          }
          .addclass {
            color: #fe7959;
          }
          i {
            font-size: 18rem;
            color: #999;
            margin-right: 25rem;
          }
        }
        .text {
          font-size: 14rem;
          color: #72727b;
          display: flex;
          flex-wrap: wrap;
          padding: 7.5rem 0 7.5rem 7.5rem;
          .text-i {
            span {
              line-height: 20rem;
              padding: 7.5rem;
              white-space: nowrap;
              display: inline-block;
            }
          }
        }
      }
    }
  }
}
</style>
